.projectName{
    margin-top: 50px;
    padding: 20px 0px;
    background-color: rgba(150, 170, 237, 0.5);
    backdrop-filter: blur(5px);
}

.projectDescription{
    margin-top: 50px;
}
.projectDescription h2{
    margin-bottom: 30px;
}
.projectFeatures ul{
    padding: 0;
    text-align: left;
    margin-bottom: 30px;
}

.projectFeatures ul li{
    margin: 10px 0px !important;
}
.projectDescription p{
    text-align: left;
}
.projectDevelopers{
    margin-top: 30px;
}
.projectDevelopers .col-md-6 ,.col-md-12{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.projectDevelopersProfile{
    height: 200px;
    width: 200px;
    margin: 20px 0px;
    clip-path: circle(50%);
}
.projectDevelopersDesc{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.projectDevelopersSocial ul{
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.projectDevelopersSocial ul li{
    margin-left: 30px;
    font-size: 30px;
}
.projectDevelopersSocial ul li:nth-child(1){
    margin: 0;
}

@media screen and (max-width: 768px){
    .projectDevelopersProfile{
        height: auto;
        width: auto;
        margin: 20px 0px;
        clip-path: circle(50%);
    }
}